<template>
	<div id="tab-controll">
		<div class="tab-item" v-for="(item,index) in titles" :key="item.name">
			<span :class="{active:index===currentIndex}" @click="itemclick(index)">{{item}}</span>
		</div>
	</div>
</template>

<script>
export default {
	props: {
		titles: Array,
	},
	data() {
		return {
			currentIndex: 0,
			aa: 2,
		};
	},
	methods: {
		itemclick: function (index) {
			this.currentIndex = index;
			this.$emit('tabControlClick', index);
		},
	},
};
</script>

<style>
#tab-controll {
	display: flex;
	/* position: sticky;
	top: 44px; */
	/* z-index: 2; */
	/* margin-top: 5px; */
	height: 44px;
	line-height: 44px;
}
.tab-item {
	flex: 1;
	box-sizing: border-box;
	color: #8d8d8d;
	text-align: center;
	background-color: #fff;
}
.tab-item span {
	display: inline-block;
	height: 100%;
	width: 60px;
	font-size: 18px;
}
.active {
	color: #e6335f;
	border-bottom: 4px solid #e6335f;
}
</style>