<template>
	<div class="back-top" @click="topClick">
		<img src="../../../assets/img/common/top.png" alt />
	</div>
</template>

<script>
export default {
	name: 'BackTop',
	methods: {
		topClick: function () {
			this.$emit('backTop');
		},
	},
};
</script>

<style>
.back-top {
	width: 43px;
	height: 43px;
	text-align: center;
}

.back-top img {
	position: fixed;
	bottom: 70px;
	right: 20px;
	width: 43px;
	height: 43px;
	border-radius: 140%;
	box-shadow: 0 0 10px 0 hsla(0, 6%, 58%, 0.6);
}
</style>
