<template>
	<div id="home">
		<!-- 上导航栏 -->
		<nav-bar>
			<template #center>
				<h2>购物街</h2>
			</template>
		</nav-bar>
		<tab-controll
			:titles="['流行','新款','精选']"
			@tabControlClick="tabControlClick"
			ref="tabcontrol1"
			v-show="isTabFixed"
			class="tabcontrol1"
		></tab-controll>

		<scroll
			class="content"
			:probetype="3"
			@scrollListener="contentScroll"
			ref="scroll"
			:pullupload="true"
			@pullingUp="loadMore"
		>
			<template>
				<!-- 轮播图 -->
				<swiper>
					<swiper-item v-for="item in banner" :key="item.name">
						<a :href="item.link">
							<img :src="item.image" alt @load="swiperImgLoad" />
						</a>
					</swiper-item>
				</swiper>
				<!-- 推荐信息 -->
				<recommend-view :recommend="recommend"></recommend-view>
				<!-- 本周流行 -->
				<feature-view></feature-view>
				<!-- 商品导航 -->
				<tab-controll :titles="['流行','新款','精选']" @tabControlClick="tabControlClick" ref="tabcontrol2"></tab-controll>
				<!-- 商品列表 -->
				<goods-list :goods="showgoods"></goods-list>
			</template>
		</scroll>
		<!-- 返回顶部按钮 -->
		<back-top v-show="isShowBackTop" @backTop="backToTop"></back-top>
	</div>
</template>

<script>
import { getHomeMultidata, getHomeData } from '../../../network/home';
import NavBar from '../../../components/common/navbar/NavBar';
import { Swiper, SwiperItem } from '../../../components/common/swiper/';
import RecommendView from './ChildComponent/recommend-view';
import FeatureView from './ChildComponent/feature-view';
import TabControll from '../../../components/content/TabControll';
import GoodsList from '../../../components/content/goods/GoodsList';
import Scroll from '../../../components/common/scroll/scroll';
import BackTop from '../../../components/content/backTop/BackTop';
import { debounce } from '../../../common/utils/utils';
export default {
	components: {
		NavBar,
		Swiper,
		SwiperItem,
		RecommendView,
		FeatureView,
		TabControll,
		GoodsList,
		Scroll,
		BackTop,
	},
	data() {
		return {
			banner: [],
			recommend: [],
			goods: {
				pop: { page: 0, list: [] },
				new: { page: 0, list: [] },
				sell: { page: 0, list: [] },
			},
			goodsIndex: 'pop',
			isShowBackTop: false,
			tabOffsetTop: 0,
			isTabFixed: false,
			saveY: 0,
		};
	},
	created() {
		this.getHomeMultidata();

		this.getHomeData('pop');
		this.getHomeData('new');
		this.getHomeData('sell');
	},
	activated() {
		this.$refs.scroll.refresh();
		this.$refs.scroll.scrollTo(0, this.saveY, 300);
	},
	deactivated() {
		this.saveY = this.$refs.scroll.scroll.y;
	},
	mounted() {
		// this.tabOffsetTop=
		const refresh = debounce(this.$refs.scroll.refresh, 500);
		this.$bus.$on('itemImgLoad', () => {
			// this.$refs.scroll && this.$refs.scroll.refresh();
			refresh();
		});
	},
	computed: {
		showgoods() {
			return this.goods[this.goodsIndex].list;
		},
	},
	methods: {
		//事件监听相关方法
		tabControlClick: function (index) {
			switch (index) {
				case 0:
					this.goodsIndex = 'pop';
					break;
				case 1:
					this.goodsIndex = 'new';
					break;
				case 2:
					this.goodsIndex = 'sell';
					break;
			}

			this.$refs.tabcontrol1.currentIndex = index;
			this.$refs.tabcontrol2.currentIndex = index;
		},
		contentScroll: function (pos) {
			this.isShowBackTop = -pos.y > 688;
			this.isTabFixed = -pos.y > this.tabOffsetTop;
		},
		backToTop: function () {
			this.$refs.scroll.scrollTo(0, 0);
		},
		loadMore: function () {
			this.getHomeData(this.goodsIndex);
		},
		swiperImgLoad: function () {
			this.tabOffsetTop =
				this.$refs.tabcontrol2.$el.offsetTop -
				this.$refs.tabcontrol2.$el.offsetHeight;
		},

		// 网络请求相关方法
		getHomeMultidata: function () {
			getHomeMultidata().then((res) => {
				this.banner = res.data.banner.list;
				this.recommend = res.data.recommend.list;
			});
		},
		getHomeData: function (type) {
			const page = this.goods[type].page + 1;
			getHomeData(type, page).then((res) => {
				this.goods[type].list.push(...res.data.list);
				this.goods[type].page += 1;
				this.$refs.scroll.finishPullUp();
			});
		},
	},
};
</script>

<style>
#home {
	/* padding-top: 44px; */
	position: relative;
	height: 100vh;
}
.content {
	height: calc(100% - 93px);
	overflow: hidden;
	/* margin-top: 44px; */
}
.tabcontrol1 {
	position: fixed;
	top: 43px;
	left: 0;
	right: 0;
	z-index: 2;
}
</style>